import React from 'react';
import { ActionButton } from 'office-ui-fabric-react';
import useTheme from '../../Theme';
import useI18n from '../../I18n';
import useAPI from '../../API';

import $ from './style.scss';
import D from './en.d.yml';

const Footer: React.SFC = () => {
  /* States */
  const { t, setLocale, getLocale } = useI18n(D);
  const { setTheme, getTheme } = useTheme();
  const { setAPILocale, fetchMenu } = useAPI();

  /* Brightness Mode */
  // Cookie manager
  const setNightBoy = (data?: boolean) => {
    if (typeof data !== 'boolean') {
      document.cookie = 'NightBoy=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
    } else {
      document.cookie = `NightBoy=${data.toString()};expires=${
        (new Date(Date.now() + 5184000)).toUTCString() // 60 days
      };`;
    }
  };
  // Button click action for toggle
  const toggleDispBrightnessMode = () => {
    if (document.documentElement.dataset?.theme === 'dark') {
      setTheme('light');
      if (window.matchMedia('(prefers-color-scheme: dark)').matches) {
        setNightBoy(false);
      } else {
        setNightBoy();
      }
    } else {
      setTheme('dark');
      if (window.matchMedia('(prefers-color-scheme: dark)').matches) {
        setNightBoy();
      } else {
        setNightBoy(true);
      }
    }
  };

  /* Delivery Menu */
  const movePolivery = () => {
    window.location.href = 'http://polivery.ml/?search';
  };

  /* Locale Change */
  // Cookie manager
  const setEnglishFlag = (data?: boolean) => {
    if (typeof data !== 'boolean') {
      document.cookie = 'EnglishFlag=;expires=Thu, 01 Jan 1970 00:00:01 GMT;';
    } else {
      document.cookie = `EnglishFlag=${data.toString()};expires=${
        (new Date(Date.now() + 5184000)).toUTCString() // 60 days
      };`;
    }
  };
  // Button click action for toggle
  const toggleDispLocale = () => {
    if (getLocale() === 'ko') {
      setLocale('en');
      setAPILocale('en');
      fetchMenu('en');
      if (window.navigator.language.indexOf('ko') >= 0) {
        setEnglishFlag(true);
      } else {
        setEnglishFlag();
      }
    } else {
      setLocale('ko');
      setAPILocale('ko');
      fetchMenu('ko');
      if (window.navigator.language.indexOf('ko') >= 0) {
        setEnglishFlag();
      } else {
        setEnglishFlag(false);
      }
    }
  };

  /* Render */
  return (
    <footer>
      <div>
        <ActionButton onClick={movePolivery} title={t('Polivery Î∞îÎ°úÍ∞ÄÍ∏∞')}>
          {t('Î∞∞Îã¨ ÏùåÏãù')}
        </ActionButton>
        {/* <ActionButton disabled>ÍµêÏßÅÏõê ÏãùÎãπ Î≥¥Í∏∞</ActionButton> */}
        <ActionButton onClick={toggleDispBrightnessMode} title={t('Î∞ùÍ∏∞ Î™®Îìú Î≥ÄÍ≤Ω')}>
          {getTheme() === 'dark' ? t('Î∞ùÍ≤å Î≥¥Í∏∞') : t('Ïñ¥Îë°Í≤å Î≥¥Í∏∞')}
        </ActionButton>
        <ActionButton onClick={toggleDispLocale}>
          {getLocale() === 'ko' ? 'English' : 'ÌïúÍµ≠Ïñ¥'}
        </ActionButton>
        {/* <ActionButton disabled>API</ActionButton> */}
      </div>
      <p>
        {t('Îç∞Ïù¥ÌÑ∞: Ìè¨Ïä§ÌÖç Î≥µÏßÄÌöå, Ïã†ÏÑ∏Í≥ÑÌë∏Îìú.')}
        <br />
        {t('ÏãúÏä§ÌÖú: ÏïÑÏßÄÎûòÍ≤¨.')}
      </p>
      <p className={$.version}>20200225.1</p>
    </footer>
  );
};

export default Footer;
